


.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
}

.footer-logo {
    display: flex;

    flex-direction: column;

    align-items: center;
    gap: 20px;
}

.footer-logo p {
    color: black;
    font-size: 46px;
    font-weight: 700;
}

.footer-links {
    display: flex;
    list-style: none;
    gap: 50px;
    color: black;
    font-size: 23px;
    font-weight: 300;
}

.footer-links li {
    cursor: pointer;
    transition: .5s;
    -webkit-transition: .5s;
    -moz-transition: .5s;
    -ms-transition: .5s;
    -o-transition: .5s;
}

.footer-links li:hover{
    color: #ff4141;
}

.footer-social-icon {
    display: flex;
    gap: 20px;

}

.footer-icons-container {
    padding: 10px;
    padding-bottom: 6px;
    /* background: #fbfbfb; */
    /* border: 1px solid #ebebeb; */
}

 .footer-copyright p {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-bottom: 35px;
    color: #1a1a1a;
    font-size: 20px;
    font-weight: 100;
}

.footer-copyright hr{
    margin-bottom: 10px;
    width: 100%;
    border: none;
    height: 2px;
    background: #c7c7c7;
    border-radius: 10px;
   
}