.popular{
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    height: 90vh;
}
.popular h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
    margin-top: 15px;
}
.popular hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    background: #0f0e0e;
    margin-bottom: 10px;
}
.popular-item{
    margin-top: 50px;
    display: flex;
    gap: 30px;
}