


.cartitems{
    margin: 100px 170px;
}
.cartitems hr{
    height: 2px;
    background: #e2e2e2;
    border: 0;
}
.cartitems-format-main{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 75px;
    padding: 20px 0;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}
.cartitems-format{
    font-size: 17px;
    font-weight: 500;
}
.carticon-product-icon{
    height: 60px;
}
.cartitems-remove-icon{
    width: 15px;
    margin: 0px 40px;
    cursor: pointer;
}
.cartitems-quantity{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: #fff;
    font-size: 15px;
}
.cartitems-down{
    display: flex;
    margin: 100px 0px;
}
.cartitems-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    gap: 40px;
}
.cartitems-total-item{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}
.cartitems-total button{
    width: 262px;
    height: 58px;
    outline: none;
    border: none;
    color: #fff;
    background: #ff5a5a;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: .5s ease-in-out;
    -webkit-transition: .5s ease-in-out;
    -moz-transition: .5s ease-in-out;
    -ms-transition: .5s ease-in-out;
    -o-transition: .5s ease-in-out;
}
.cartitems-total button:hover{
    color: #ff5a5a;
    background: #fff;
    border: 1px solid #ff5a5a;
}
.cartitems-promocode{
    flex: 1;
    font-size: 15px;
    font-weight: 500;

}
.cartitems-promocode p{
    color: #555;
}
.cartitems-promobox{
    width: 504px;
    margin-top: 17px;
    padding-left: 20px;
    height: 59px;
    background: #eaeaea;
}
.cartitems-promobox input{
    border: none;
    background: transparent;
    outline: none;
    font-size: 15px;
    width: 330px;
    height: 50px;
}
.cartitems-promobox button{
    width: 174px;
    height: 58px;
    font-size: 15px;
    background: #000;
    color: #fff;
    cursor: pointer;
    outline: none;
    border: none;
    transition: .3s ease;
    -webkit-transition: .3s ease;
    -moz-transition: .3s ease;
    -ms-transition: .3s ease;
    -o-transition: .3s ease;
    font-weight: 700;
}
.cartitems-promobox button:hover{
    color: #000;
    background: transparent;
    border: 1px solid #000;
}